"use client";
import ClassroomDetail from '@/components/dashboard/classroomDetail';
import { useParams } from 'next/navigation';

const Page = () => {
  const params = useParams();
  const classroomId = params?.id as string;

  return (
    <div className="min-h-[calc(100vh-40px)] flex flex-col items-center bg-[#f8f8ff] py-8">
      <div className="w-full max-w-6xl">
        <div className="rounded-2xl bg-white p-7 shadow">
          <ClassroomDetail classroomId={classroomId} />
        </div>
      </div>
    </div>
  );
};

export default Page;